import {  Divider, Input, InputNumber, List, Typography } from "antd";
import React, { useState, useEffect } from "react";
import { utils } from "ethers";
// import { FontSizeOutlined, SyncOutlined, WindowsOutlined } from "@ant-design/icons";
import { useContractReader } from "eth-hooks";
import { Address, Balance, Events } from "../components";
import InfiniteScroll from 'react-infinite-scroll-component';

const { Text } = Typography;
export default function YaafMinerUI({
  // purpose,
  localProviderPollingTime,
  address,
  mainnetProvider,
  localProvider,
  yourLocalBalance,
  price,
  tx,
  readContracts,
  writeContracts,
}) {
  const [newPurpose, setNewPurpose] = useState("0.01");
  const [reflink, setReflink] = useState("")
  const [refAddress, setRefAddress] = useState("")
  // const [state, copyToClipboard] = useCopyToClipboard();
  useEffect(() => {
    const urllink = window.location.href
    if (urllink.indexOf('?') != -1) {
      const ary1 = urllink.split('?id=')
      setReflink(ary1[0] + "?id=" + address)
      setRefAddress(ary1[1])
    } else {
      setReflink(urllink + "?id=" + address)
      setRefAddress(address)
    }

  }, [address]);

  // const purpose = useContractReader(readContracts, "YourContract", "purpose");
  const startTime = useContractReader(readContracts, "YaafMiner", "startTime", [], localProviderPollingTime);
  const currentTime = useContractReader(readContracts, "YaafMiner", "TimeCheck", [], localProviderPollingTime);
  const referralData = useContractReader(readContracts, "YaafMiner", "getReferralData", [address], localProviderPollingTime);
  // console.log("%%%%%%%%%%" + JSON.stringify(referralData[0]))

  const poolAmount = useContractReader(readContracts, "YaafMiner", "getPoolAmount", [], localProviderPollingTime);
  const breedingBreeders = useContractReader(readContracts, "YaafMiner", "getBreedingBreeders", [address,], localProviderPollingTime);
  const YaafMinerRewards = useContractReader(readContracts, "YaafMiner", "YaafMinerRewards", [address,], localProviderPollingTime);


  const referralAllRebate = useContractReader(readContracts, "YaafMiner", "getReferralAllRebate", [address], localProviderPollingTime);
  const getClaimYaafMiner = useContractReader(readContracts, "YaafMiner", "getClaimYaafMiner", [address], localProviderPollingTime);
  const calculateYaafMinerMerge = useContractReader(readContracts, "YaafMiner", "calculateYaafMinerMerge", [getClaimYaafMiner ? getClaimYaafMiner : 0,], localProviderPollingTime);
  const referralAllInvitee = useContractReader(readContracts, "YaafMiner", "getReferralAllInvitee", [address], localProviderPollingTime);

  return (
    <div>
      <div className="countdown_block">
        <div id="countdown">
          <ul>
            <li><span className="text d-text">DAY</span><span id="days" className="time">{startTime && currentTime && Math.abs(startTime - currentTime) > 0 ? parseInt(Math.abs(startTime - currentTime) / 86400) : "0"}</span></li>
            :
            <li><span className="text hr-text">HRS</span><span id="hours" className="time">{startTime && currentTime && Math.abs(startTime - currentTime) > 0 ? parseInt(Math.abs(startTime - currentTime) % 86400 / 3600) : "0"}</span></li>
            :
            <li><span className="text m-text">MIN</span><span id="minutes" className="time">{startTime && currentTime && Math.abs(startTime - currentTime) > 0 ? parseInt(Math.abs(startTime - currentTime) % 86400 % 3600 / 60) : "0"}</span></li>
            :
            <li><span className="text s-text">SEC</span><span id="seconds" className="time">{startTime && currentTime && Math.abs(startTime - currentTime) > 0 ? parseInt(Math.abs(startTime - currentTime) % 86400 % 3600 % 60) : "0"}</span></li>
          </ul>
        </div>
      </div>
      <div id="yaaf">
        <div className="block block1">
          <div className="col">
            <div className="left contract-text">Contract</div>
            <div className="right" id="BNBBalance">{poolAmount ? parseFloat(utils.formatEther(poolAmount)).toFixed(4) : "0"} BNB</div>
          </div>
          <div className="col">
            <div className="left wallet-text">Wallet</div>
            <div className="right" id="WalletBalance">{yourLocalBalance ? parseFloat(utils.formatEther(yourLocalBalance)).toFixed(4) : "0"}  BNB</div>
          </div>
          <div className="col">
            <div className="left yaaf-text">Your YAAF</div>
            <div className="right" id="YAAF_MinerAmount">{breedingBreeders ? String(breedingBreeders) : "0"} YAAF</div>
          </div>
          <InputNumber
            style={{
              width: "100%",
              border: "0px solid rgb(111, 108, 108)",
            }}
            // defaultValue="0.01"
            value={newPurpose}
            min="0.01"
            max="99999999"
            step="0.00001"
            onChange={value => {
              if (/^\d+\.\d*$|^$/.test(value)) {
                setNewPurpose(value);
              }
            }}
            stringMode
          />
          <br />
          <button className="create_btn" onClick={async (e) => {
            if (e.target.tagName === "INPUT") {
              return;
            }
            if (newPurpose === "") {
              alert("Please enter a value.");
              return;
            }

            const result = tx(
              writeContracts.YaafMiner.createYaafMiner(refAddress, {
                value: utils.parseEther(newPurpose),
              }),
              update => {
                console.log(" Transaction Update:", update);
                if (update && (update.status === "confirmed" || update.status === 1)) {
                  console.log("  Transaction " + update.hash + " finished!");
                  console.log(
                    "  " +
                    update.gasUsed +
                    "/" +
                    (update.gasLimit || update.gas) +
                    " @ " +
                    parseFloat(update.gasPrice) / 1000000000 +
                    " gwei",
                  );
                }
              });
            console.log("awaiting metamask/web3 confirm result...", result);
            console.log(await result);
          }}>
            Stake
          </button>
          <Divider />
          <div className="col">
            <div className="left rewards-text">Your Rewards</div>
            <div className="right" id="YourRewards">{YaafMinerRewards ? parseFloat(utils.formatEther(YaafMinerRewards)).toFixed(4) : "0"} BNB</div>
          </div>
          <Divider />
          <div className="buttons">
            <button className="cultivate_btn" onClick={async () => {
              const result = tx(
                writeContracts.YaafMiner.divideYaafMiner(refAddress),
                update => {
                  console.log(" Transaction Update:", update);
                  if (update && (update.status === "confirmed" || update.status === 1)) {
                    console.log("  Transaction " + update.hash + " finished!");
                    console.log(
                      "  " +
                      update.gasUsed +
                      "/" +
                      (update.gasLimit || update.gas) +
                      " @ " +
                      parseFloat(update.gasPrice) / 1000000000 +
                      " gwei",
                    );
                  }
                });
              console.log("awaiting metamask/web3 confirm result...", result);
              console.log(await result);
            }}
            >
              Re-Stake
            </button>
            <button className="party_btn" onClick={async () => {
              const result = tx(
                writeContracts.YaafMiner.mergeYaafMiner(),
                update => {
                  console.log(" Transaction Update:", update);
                  if (update && (update.status === "confirmed" || update.status === 1)) {
                    console.log("  Transaction " + update.hash + " finished!");
                    console.log(
                      "  " +
                      update.gasUsed +
                      "/" +
                      (update.gasLimit || update.gas) +
                      " @ " +
                      parseFloat(update.gasPrice) / 1000000000 +
                      " gwei",
                    );
                  }
                });
              console.log("awaiting metamask/web3 confirm result...", result);
              console.log(await result);
            }}
            >
              Claim
            </button>
          </div>
        </div>

        <div className="block block2">
          <h2 className="nutrient-btn">Expected Profit</h2>
          <Divider />
          <div className="info">
            <div className="pic">
              <img src="img/yaaf-logo1.png" alt="" />
            </div>
            <div className="infoblock">
              <div className="col">
                <div className="left d-return-text">Daily Return</div>
                <div className="right">5%</div>
              </div>
              <div className="col">
                <div className="left y-return-text">APR</div>
                <div className="right">1825%</div>
              </div>
              <div className="col">
                <div className="left dev-fee-text">Dev Fee</div>
                <div className="right">3%</div>
              </div>
              <div className="col">
                <div className="left m-fee-text">Marketing Fee</div>
                <div className="right">3%</div>
              </div>
            </div>
          </div>
          <Divider />
          <h2 className="link-text">Referral Link</h2>
          <Divider />
          <p className="receive-text">Earn 10% of the BNB used to Divide YAAF from anyone who uses your referral link</p>

          <Text copyable={{ text: reflink }} style={{ fontSize: "20px" }}>

            <Input type="text" id="refLint" value={reflink} readOnly={true} />

          </Text>
          {/* <div className="col">
            <div className="left invite-text">Your Invites</div>
            <div className="right" id="inv">{referralAllInvitee ? String(referralAllInvitee) : "0"}</div>
          </div>
          <div className="col">
            <div className="left rebate-text">Your Rebate</div>
            <div className="right" id="rat">{referralAllRebate && calculateYaafMinerMerge ? parseFloat(utils.formatEther(referralAllRebate) + utils.formatEther(calculateYaafMinerMerge)).toFixed(4) : "0"} BNB</div>
          </div> */}
        </div>
      </div>

      {/*
         Maybe display a list of events?
          (uncomment the event and emit line in YaafMiner.sol! )
      */}

      <div id="yaafref">
        <div className="block block1">
          <Events
            contracts={readContracts}
            contractName="YaafMiner"
            eventName="Create"
            localProvider={localProvider}
            mainnetProvider={mainnetProvider}
            startBlock={1}
            address={address}
          />
        </div>

        <div className="block block2">
          <h2 className="nutrient-btn">My Team</h2>
          <Divider />
          <div className="col">
            <div className="left invite-text">My Invites</div>
            <div className="right" id="inv">{referralAllInvitee ? String(referralAllInvitee) : "0"}</div>
          </div>
          <div className="col">
            <div className="left rebate-text">My Commission</div>
            <div className="right" id="rat">{referralAllRebate && calculateYaafMinerMerge ? parseFloat(utils.formatEther(referralAllRebate) + utils.formatEther(calculateYaafMinerMerge)).toFixed(4) : "0"} BNB</div>
          </div>
          <div className="col">

            <div
              id="scrollableDiv"
              style={{
                height: 200,
                overflow: 'auto',
                padding: '0 0px',
                border: '1px solid rgba(140, 140, 140, 0.35)',
              }}
            >
              {referralData ?
                <InfiniteScroll
                  dataLength={referralData[0].length}
                  // next={loadMoreData}
                  hasMore={referralData[0].length < 4}
                  // loader={<Skeleton avatar paragraph={{ rows: 1 }} active />}
                  endMessage={<Divider plain>It is all, nothing more </Divider>}
                  scrollableTarget="scrollableDiv"
                >

                  <List
                    bordered
                    dataSource={referralData[0]}
                    renderItem={item => {
                      return (
                        <List.Item key={item + "_" + address}>
                          <Address address={item} ensProvider={mainnetProvider} fontSize={14} />
                        </List.Item>
                      );
                    }}
                  />
                </InfiniteScroll> :
                <InfiniteScroll
                  dataLength={0}
                  hasMore={false}
                  scrollableTarget="scrollableDiv"
                >
                  <List />
                </InfiniteScroll>
              }
            </div>

          </div>

        </div>
      </div>

    </div>
  );
}
