import { Divider, List } from "antd";
import { useEventListener } from "eth-hooks/events/useEventListener";
import Address from "./Address";
import { utils } from "ethers";
import InfiniteScroll from 'react-infinite-scroll-component';

/**
  ~ What it does? ~

  Displays a lists of events

  ~ How can I use? ~

  <Events
    contracts={readContracts}
    contractName="YourContract"
    eventName="SetPurpose"
    localProvider={localProvider}
    mainnetProvider={mainnetProvider}
    startBlock={1}
  />
**/

export default function Events({ contracts, contractName, eventName, localProvider, mainnetProvider, startBlock, address }) {
  //  Listen for broadcast events
  const events = useEventListener(contracts, contractName, eventName, localProvider, startBlock);
  const myevents = events.filter((item)=>{
    return item.args[0] === address
  }
  

  )
  // console.log(JSON.stringify(myevents))
  return (
    <div style={{ width: "100%", margin: "auto", paddingBottom: 32 }}>
      <h2>My Logs:</h2>
      <Divider />
      <div
        id="scrollableDiv"
        style={{
          height: 300,
          overflow: 'auto',
          padding: '0 0px',
          border: '1px solid rgba(140, 140, 140, 0.35)',
        }}
      >
        <InfiniteScroll
          dataLength={myevents.length}
          // next={loadMoreData}
          hasMore={myevents.length < 5}
          // loader={<Skeleton avatar paragraph={{ rows: 1 }} active />}
          endMessage={<Divider plain>It is all, nothing more </Divider>}
          scrollableTarget="scrollableDiv"
        >
          <List
            bordered
            dataSource={myevents}
            renderItem={item => {
              return (
                <List.Item key={item.blockNumber + "_" + item.args.sender + "_" + item.args.purpose}>

                  <Address address={item.args[0]} ensProvider={mainnetProvider} fontSize={14} />
                  {String(parseFloat(utils.formatEther(item.args[1])).toFixed(4))}

                </List.Item>
              );
            }}
          />
        </InfiniteScroll>
      </div>
    </div>
  );
}
